import React, { Component } from 'react'
import { Icon } from 'antd'
import { withRouter } from 'react-router-dom';
class Theader extends Component {
    state={
        menus:[],
        currentMenu:''
    }
    componentDidMount = () => {
        let menus=[{
            id:'0001',
            title:'达标指标',
            url:'/dabiao',
        },{
            id:'0002',
            title:'向好指标',
            url:'/xianghao',
        },{
            id:'0003',
            title:'向差指标',
            url:'/xiangcha',
        },{
            id:'0004',
            title:'预警指标',
            url:'/yujing',
        },{
            id:'0005',
            title:'学校视角',
            url:'/xuexiao',
        },{
            id:'0006',
            title:'教师视角',
            url:'/jiaoshi',
        },{
            id:'0007',
            title:'学生视角',
            url:'/xuesheng',
        },{
            id:'0008',
            title:'专业视角',
            url:'/zhuanye',
        },{
            id:'0009',
            title:'课程视角',
            url:'/kecheng',
        }]
        let data=menus.map((item,index)=>{
            item.key=index+1
            return item
        })
        // if(localStorage.getItem('menuItem')){
        //     this.setState({
        //         menus:data,
        //         currentMenu:JSON.parse(localStorage.getItem('menuItem')).title
        //     })
        // }else{
        //     this.setState({
        //         menus:data,
        //         currentMenu:menus[0].title
        //     })
        // }
        // 暂时注释掉localstoreage,暂时不做持久化 
        this.setState({
            menus:data,
            currentMenu:menus[0].title
        })
        
    }
    // 处理页面跳转，同时将当前的菜单信息存入本地
    handleGoTo=(item)=>{
        localStorage.setItem('menuItem',JSON.stringify(item))
        this.setState({
            currentMenu:item.title
        })
        this.props.history.push(item.url)
        // console.log(url)
    }
    render() {
        const {menus,currentMenu}=this.state
        return (
            <div className='header-wrapper'>
                <div className='home'>
                    <Icon type="home" theme='filled' style={{ fontSize: 27, color: '#fff', marginLeft: 20, cursor: 'pointer' }} />
                </div>
                <div className='other'>
                    <div className='other-title'>
                        教学诊断与改进平台
            </div>
                    <div className='other-menu'>
                        {currentMenu}
                <div className='other-sub-menu'>
                            {
                                menus.map(item=><div key={item.key} onClick={()=>{this.handleGoTo(item)}} className={currentMenu === item.title ? 'item active':'item'}>{item.title}</div>)
                            }
                        </div>
                    </div>
                    <div className='other-line'>

                    </div>
                </div>
            </div>
        )
    }
}


export default withRouter(Theader)