import React, { Component } from 'react'
import {Switch, Route, Redirect} from 'react-router-dom';
import './index.less'
import Theader from './header'
import { asyncComponent } from '../../utils';

// 达标指标
const Dabiao = asyncComponent(() => import('../dabiao'));
const Xianghao = asyncComponent(() => import('../xianghao'));
const Xiangcha = asyncComponent(() => import('../xiangcha'));
const Yujing = asyncComponent(() => import('../yujing'));
const Xuexiao = asyncComponent(() => import('../xuexiao'));
const Jiaoshi = asyncComponent(() => import('../jiaoshi'));
const Xuesheng = asyncComponent(() => import('../xuesheng'));
const Zhuanye = asyncComponent(() => import('../zhuanye'));
const Kecheng = asyncComponent(() => import('../kecheng'));
export default class Home extends Component {
    render() {        
        return (
            <div className='main-wrapper'>
                <Theader></Theader>
                <div className='body-wrapper'>
                    <Switch>
                        <Route path="/dabiao" exact component={Dabiao}/>      
                        <Route path="/xianghao" exact component={Xianghao}/>
                        <Route path="/xiangcha" exact component={Xiangcha}/>
                        <Route path="/yujing" exact component={Yujing}/>  

                        <Route path="/xuexiao" exact component={Xuexiao}/>  
                        <Route path="/jiaoshi" exact component={Jiaoshi}/>  
                        <Route path="/xuesheng" exact component={Xuesheng}/>  
                        <Route path="/zhuanye" exact component={Zhuanye}/>  
                        <Route path="/kecheng" exact component={Kecheng}/>  
                        {/* 暂时重定向到达标指标页面                 */}
                        <Route path="/" render={(props) => <Redirect to='/dabiao'/>}/>
                    </Switch>

                </div>
            </div>
        )
    }
}
